<template>
	<div class="max-h-96 overflow-y-auto">
    <BBTable :items="items" :selectable="true" :check-all="true" :header="['Item #', 'Price']" @selection-change="handleSelectionChange" collapse-all>
      <template #item="{item}">
        <BBTColumn>
          <p class="font-semibold text-teal-04">{{item.product_name}}</p>
          <small class="block"><span class="font-semibold">IMEI/SN:</span> {{item.imei_sn || '-'}}</small>
          <small class="block">{{item.detail || '-'}}</small>
        </BBTColumn>
        <BBTColumn>
          ${{ money(item.price, { currency: me.currency })}} aaa
        </BBTColumn>
      </template>
    </BBTable>
	</div>
</template>

<script>
import { defineComponent, ref } from 'vue'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'
import BBTable from 'components/Table/BBTable.vue'
import BBTColumn from 'components/Table/BBTColumn.vue'
import { money } from 'utils/number'
import { useUser } from 'composable/user'

export default defineComponent({
  components: {
    Input,
    Select,
    BBTable,
    BBTColumn
  },
  props: {
    items: {
      type: Array,
      default: () => ([])
    }
  },
  emits: ['selection-change'],
  setup(props, { emit }) {
    const { me } = useUser()
    const selectedItems = ref(props.items)
    const handleSelectionChange = (items) => {
      selectedItems.value = items
      emit('selection-change', selectedItems.value)
    }
    return {
      me,
      selectedItems,
      handleSelectionChange,
      money
    }
  }
})
</script>