<template>
	<div>
    <Select @input="handleSelectionChange" class="w-full" :items="conditions" name="condition_name" by="condition_name" label="Change condition to" />
	</div>
</template>

<script>
import { defineComponent } from 'vue'
// import { useDetail } from 'composable/invoice'
import Input from 'bbUi/components/input/input.vue'
import Select from 'bbUi/components/select/select.vue'

export default defineComponent({
  components: {
    Input,
    Select
  },
  props: {
    conditions: {
      type: Array,
      default: () => ([])
    }
  },
  emits: ['selection-change'],
  setup(props, { emit }) {
    const handleSelectionChange = (condition) => emit('selection-change', condition)
    return {
      handleSelectionChange
    }
  }
})
</script>